body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header {
  background-color: #282c34;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}
/* .logo {
  flex-grow: 0;
} */
.search-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.app-search {
  height: 30px;
  margin: 10px 0 10px;
  width: 250px;
  border: 1px solid #282c34;
  border-radius: 30px;
  padding: 5px 15px;
  outline: 0;
}
.jobdetail_container {
    text-align: left;
    max-width: 800px;
    margin: 0 auto;
    padding-top: 80px;
}
.backBtn {
    padding: 10px 20px;
    background: #fff;
    border: 1px solid #000;
    border-radius: 20px;
}
div, p h1, h2, h3, h4, h5{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.banner {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 25vmin;
  pointer-events: none;
  margin: 30px 0;
}
.App-link {
  color: #61dafb;
}
#listing {
    max-width: 600px;
    margin: 35px auto;
}
.joblist {
    padding: 18px 10px;
    border-top: 1px solid #ddd;
    text-align: left;
    list-style: none;
    font-weight: 300;
}
.joblist_title{
    line-height: 1.5rem;
    font-weight: 400;
    font-size: 1.2em;
    color: #01c1df;
}

.joblist_company {
  font-size: 1rem;
    margin: 15px 0 10px;
}
.joblist_location {
    font-size: .9rem;
    color: #919191;
    padding: 0 1rem 0 0;
}
.joblist_created {
      font-size: .9rem;
    color: #919191;
    padding: 0 1rem 0 0;
}
.loader {
  height: 4px;
  max-width: 400px;
  position: relative;
  overflow: hidden;
  background-color: #ddd;
  margin: 50px auto;
}
.loader:before{
  display: block;
  position: absolute;
  content: "";
  left: -200px;
  width: 200px;
  height: 4px;
  background-color: #2980b9;
  -webkit-animation: loading 2s linear infinite;
          animation: loading 2s linear infinite;
}
.loader:after {
  content: 'Loading..';
  display: block;
  position: fixed;
  width: 400px;
  margin: 15px auto;
}
@-webkit-keyframes loading {
    from {left: -200px; width: 30%;}
    50% {width: 30%;}
    70% {width: 70%;}
    80% { left: 50%;}
    95% {left: 120%;}
    to {left: 100%;}
}
@keyframes loading {
    from {left: -200px; width: 30%;}
    50% {width: 30%;}
    70% {width: 70%;}
    80% { left: 50%;}
    95% {left: 120%;}
    to {left: 100%;}
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

